import React, { useEffect, useState } from 'react'
import DateTimePicker from 'react-datetime-picker';
import Modal from 'react-modal';
import moment from 'moment'
import Swal from 'sweetalert2';
import { uiCloseModal } from '../../actions/ui';
import { useDispatch, useSelector } from 'react-redux';
import { eventStartAddNew, eventClearActiveEvent, eventStartUpdate } from '../../actions/events';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
};

Modal.setAppElement('#root');

const now = moment().minutes(0).seconds(0).add(1,'hours')
const initEvent = {
    title:'',
    notes:'',
    start:now.toDate(),
    end:moment(now).add(1, 'hours').toDate()
}

const CalendarModal = () => {
    const dispatch = useDispatch()

    const {modalOpen} = useSelector(state=>state.ui)
    const {activeEvent} = useSelector(state=>state.calendar)
    const [titleValid, setTitleValid] = useState(true)
    const [dateStart, setDateStart] = useState(now.toDate())
    const [dateEnd, setDateEnd] = useState(moment(dateStart).add(1, 'hours').toDate())

    const [formValues, setFormValues] = useState(initEvent)

    const {notes, title, start, end} = formValues

    useEffect(() => {
        if(activeEvent){
            setFormValues(activeEvent)
        }else{
            setFormValues(initEvent)
        }
    }, [activeEvent, setFormValues])


    const handleInputChange = ({target}) => {
        setFormValues({
            ...formValues,
            [target.name]:target.value
        })
    }

    const closeModal = () => {
        setFormValues(initEvent)
        dispatch(eventClearActiveEvent())
        dispatch(uiCloseModal())
    }

    const handleStartDate = (e) => {
        setDateStart(e)
        setFormValues({
            ...formValues,
            start:e
        })
    }

    const handleEndDate = (e) => {
        setDateEnd(e)
        setFormValues({
            ...formValues,
            end:e
        })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const momentStart = moment(start)
        const momentEnd = moment(end)
        if(momentStart.isSameOrAfter(momentEnd)) {
            //fecha 2 debe de ser mayor
            Swal.fire('Error', 'La fecha fin debe ser mayor a la fecha inicio', 'error')
            return
        }

        if(title.trim().length < 2){
            return setTitleValid(false)
        }


        //guardar en base de datos
        if(activeEvent){
            dispatch(eventStartUpdate({
                ...formValues
            }))
        }else{
            dispatch(eventStartAddNew({
                ...formValues,
            }))
        }
        setTitleValid(true)
        closeModal()
    }

    return (
        <Modal
            isOpen={modalOpen}
            onRequestClose={closeModal}
            style={customStyles}
            className="modal"
            overlayClassName="modal-fondo"
            closeTimeoutMS={200}
        >
            <h1> {activeEvent ? 'Editar Evento' : 'Nuevo evento'} </h1>
            <hr />
            <form className="container" onSubmit={onSubmit}>

                <div className="form-group">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker
                        onChange={handleStartDate}
                        value={dateStart}
                        className="form-control"
                    />
                </div>

                <div className="form-group">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker
                        onChange={handleEndDate}
                        value={dateEnd}
                        minDate={dateStart}
                        className="form-control"
                    />
                </div>

                <hr />
                <div className="form-group">
                    <label>Titulo y notas</label>
                    <input 
                        type="text" 
                        className={`form-control ${!titleValid && 'is-invalid'}`}
                        placeholder="Título del evento"
                        name="title"
                        autoComplete="off"
                        value={title}
                        onChange={handleInputChange}
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
                </div>

                <div className="form-group">
                    <textarea 
                        type="text" 
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={notes}
                        onChange={handleInputChange}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </Modal>
    )
}

export default CalendarModal
